                    hetongJieshuTime: false,
                    zhengzhiTypes: false,
                    yuangongXuexiao: false,
                    gangweiTypes: false,
                    hukou: false,
                    hunyinTypes: false,
                    zhuzhi: false,
                    gongzuoTime: false,
                    minzu: false,
                    ruzhiTime: false,
                    rudangTime: false,
                    zhuanyeTypes: false,
