                    yuangongPhone: '',
                    yuangongIdNumber: '',
                    yuangongPhoto: '',
                    sexTypes: '',
                    yuangongEmail: '',
                    bumenTypes: '',
                    xueliTypes: '',
                    zhuanyezhiwuTypes: '',
                    hetongTypes: '',
                    hetongFile: '',
                    hetongQiandingTime: '',
                    hetongJieshuTime: '',
                    zhengzhiTypes: '',
                    yuangongXuexiao: '',
                    gangweiTypes: '',
                    hukou: '',
                    hunyinTypes: '',
                    zhuzhi: '',
                    gongzuoTime: '',
                    minzu: '',
                    ruzhiTime: '',
                    rudangTime: '',
                    zhuanyeTypes: '',
                },
                sexTypesOptions : [],
                bumenTypesOptions : [],
                xueliTypesOptions : [],
                zhuanyezhiwuTypesOptions : [],
                hetongTypesOptions : [],
                zhengzhiTypesOptions : [],
                gangweiTypesOptions : [],
                hunyinTypesOptions : [],
                zhuanyeTypesOptions : [],
